import {Component} from '@angular/core';
import {FocusKeyManager} from '@angular/cdk/a11y';
import {Subject} from 'rxjs';
import {IActivityManagerUpgradedComponentConfig} from '../shared/activity-manager-upgraded';

@Component({
  selector: 'app-virtual-scroll',
  templateUrl: './virtual-scroll.component.html',
  styleUrls: ['./virtual-scroll.component.css']
})
export class VirtualScrollComponent {
  items = Array.from({length: 100000}).map((_, i) => `Item #${i}`);
  FocusKeyManager = FocusKeyManager;

  activityManagerConfig: IActivityManagerUpgradedComponentConfig = {
    withWrap: true,
    withTypeAhead: {debounceInterval: 0},
  };
  keydown$ = new Subject<KeyboardEvent>();
}
