import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {RouterModule} from '@angular/router';
import {OptionsGroupListComponent} from './options-group-list/options-group-list.component';
import {DashboardComponent} from './dashboard/dashboard.component';
import {DragDropComponent} from './drag-drop/drag-drop.component';
import {FlatTreeComponent} from './tree/flat-tree/flat-tree.component';
import {DragDropNavigationComponent} from './drag-drop/drag-drop-navigation.component';
import {DemoTableComponent} from './demo-table/demo-table.component';
import {VirtualScrollComponent} from './virtual-scroll/virtual-scroll.component';
import {RootPathEnum} from './root-path.enum';
import {RootRoute} from './root-route.model';
import {NestedTreeComponent} from './tree/nested-tree/nested-tree.component';
import {LazyDropdownComponent} from './lazy-dropdown/lazy-dropdown.component';


const routes: RootRoute[] = [
  {path: RootPathEnum.OptionsGroupListComponent, component: OptionsGroupListComponent},
  // {path: RootPathEnum.DashboardComponent, component: DashboardComponent},
  {path: RootPathEnum.DragDropComponent, component: DragDropComponent},
  {path: RootPathEnum.DragDropNavigationComponent, component: DragDropNavigationComponent},
  {path: RootPathEnum.TreeComponent, component: FlatTreeComponent},
  {path: RootPathEnum.NestedTreeComponent, component: NestedTreeComponent},
  {path: RootPathEnum.DemoTableComponent, component: DemoTableComponent},
  {path: RootPathEnum.VirtualScrollComponent, component: VirtualScrollComponent},
  {path: RootPathEnum.LazyDropdownComponent, component: LazyDropdownComponent},
];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forRoot(routes),
  ]
})
export class AppRoutingModule {
}
