import {ArrayDataSource} from '@angular/cdk/collections';
import {Component} from '@angular/core';
import {NestedTreeControl} from '@angular/cdk/tree';
import {FoodNode, TREE_DATA} from './example-data';
import {ActiveDescendantKeyManager} from '@angular/cdk/a11y';
import {Subject} from 'rxjs';
import {ExampleFlatNode} from '../flat-tree/flat-tree.interface';


@Component({
  selector: 'app-nested-tree',
  templateUrl: 'nested-tree.component.html',
  styleUrls: ['nested-tree.component.css'],
})
export class NestedTreeComponent {
  KeyManager = ActiveDescendantKeyManager;
  keydown$ = new Subject<KeyboardEvent>();

  treeControl = new NestedTreeControl<FoodNode>(node => node.children);
  dataSource = new ArrayDataSource(TREE_DATA);

  hasChild = (_: number, node: FoodNode) => !!node.children && node.children.length > 0;

  labelGetter(node: ExampleFlatNode): string {
    return node.name;
  }
}
