import {Component, OnInit} from '@angular/core';
import {InfiniteDropdownComponent} from 'primeng-iow/infinite-dropdown';
import {LazyDataSource} from './lazy-scroll-data-source';
import {ActiveDescendantKeyManager} from '@angular/cdk/a11y';
import {IOptionsScrollManagerComponentConfig} from 'primeng-iow/cdk/scrolling';

@Component({
  selector: 'app-lazy-dropdown',
  templateUrl: './lazy-dropdown.component.html',
  styleUrls: ['./lazy-dropdown.component.css']
})
export class LazyDropdownComponent extends InfiniteDropdownComponent<string> implements OnInit {
  dataSource = new LazyDataSource();
  KeyManager = ActiveDescendantKeyManager;
  lazyOptionsListConfig: IOptionsScrollManagerComponentConfig = {maxHeight: 400};
  getLabel = option => option;
}
