import {AfterContentInit, Directive, forwardRef, HostListener, Input} from '@angular/core';
import {OptionsActivityManagerComponent} from 'primeng-iow/cdk/options-activity-manager';
import {IActivityManagerUpgradedComponentConfig} from './activity-manager-upgraded.interface';
import {isWithTypeAheadConfig} from './is-with-type-ahead-config.guard';
import {ListKeyManager} from '@angular/cdk/a11y';
import {Subject} from 'rxjs';

@Directive({
  selector: '[appActivityManagerDirective]',
  providers: [{
    provide: OptionsActivityManagerComponent,
    useExisting: forwardRef(() => ActivityManagerUpgradedDirective),
  }],
  exportAs: 'appActivityManagerDirective',

})
export class ActivityManagerUpgradedDirective<T> extends OptionsActivityManagerComponent<T> implements AfterContentInit {
  @Input() config: IActivityManagerUpgradedComponentConfig;
  @Input('appActivityManagerDirective') KeyManager: typeof ListKeyManager;
  keydown$ = new Subject<KeyboardEvent>();

  @HostListener('keydown', ['$event'])
  select($event: KeyboardEvent): void {
    if (this.config.selfService) {
      this.keyManager.onKeydown($event);
    }
  }

  ngAfterContentInit(): void {
    super.ngAfterContentInit();
    if (!this.config) {
      return;
    }
    if (this.config.withWrap) {
      this.keyManager.withWrap(true);
    }
    if (this.config.withTypeAhead) {
      const withTypeAheadConfig = this.config.withTypeAhead;
      const debounce = isWithTypeAheadConfig(withTypeAheadConfig) ? withTypeAheadConfig.debounceInterval : 300;
      this.keyManager.withTypeAhead(debounce);
    }
  }
}
