import {Component, OnInit, QueryList, ViewChild, ViewChildren} from '@angular/core';
import {FlatTreeControl} from '@angular/cdk/tree';
import {ActiveDescendantKeyManager} from '@angular/cdk/a11y';
import {ArrayDataSource} from '@angular/cdk/collections';
import {ExampleFlatNode} from './flat-tree.interface';
import {TREE_DATA} from './flat-tree.const';
import {OptionsActivityManagerComponent} from 'primeng-iow/cdk/options-activity-manager';
import {BaseOption} from 'primeng-iow/cdk/options';
import {IActivityManagerUpgradedComponentConfig} from '../../shared/activity-manager-upgraded';

@Component({
  selector: 'app-flat-tree',
  templateUrl: './flat-tree.component.html',
  styleUrls: ['./flat-tree.component.css']
})
export class FlatTreeComponent implements OnInit {
  KeyManager = ActiveDescendantKeyManager;
  treeControl = new FlatTreeControl<ExampleFlatNode>(node => node.level, node => node.expandable);
  data = TREE_DATA;
  dataSource = new ArrayDataSource(this.data);
  @ViewChild(OptionsActivityManagerComponent, {static: false}) activityManager: OptionsActivityManagerComponent<ExampleFlatNode>;
  @ViewChildren(BaseOption) options: QueryList<BaseOption<ExampleFlatNode>>;
  activityManagerConfig: IActivityManagerUpgradedComponentConfig = {
    withTypeAhead: {debounceInterval: 0},
    selfService: true,
  };

  get activeNode(): ExampleFlatNode {
    const activeOption = this.activityManager.keyManager.activeItem;
    return activeOption && activeOption.value;
  }

  labelGetter(node: ExampleFlatNode): string {
    return node.name;
  }

  ngOnInit(): void {
    this.treeControl.dataNodes = this.data;
  }

  onArrowLeft(): void {
    if (this.activeNode && this.treeControl.isExpandable(this.activeNode) && this.treeControl.isExpanded(this.activeNode)) {
      this.treeControl.collapseDescendants(this.activeNode);
    } else {
      const parentOption = this.options.find(option => option.value === this.getParentNode(this.activeNode));
      parentOption ? this.activityManager.keyManager.setActiveItem(parentOption) : this.activityManager.keyManager.setPreviousItemActive();
    }
  }

  onArrowRight(): void {
    if (this.activeNode && this.treeControl.isExpandable(this.activeNode) && !this.treeControl.isExpanded(this.activeNode)) {
      this.treeControl.expand(this.activeNode);
    } else {
      this.activityManager.keyManager.setNextItemActive();
    }
  }

  shouldRender(node: ExampleFlatNode) {
    return !this.hasCollapsedParent(node);
  }

  private hasCollapsedParent(node: ExampleFlatNode): boolean {
    const parent = this.getParentNode(node);
    if (!parent) {
      return false;
    }
    return !this.treeControl.isExpanded(parent) || this.hasCollapsedParent(parent);
  }

  private getParentNode(node: ExampleFlatNode): ExampleFlatNode {
    const nodeIndex = this.data.indexOf(node);
    for (let i = nodeIndex - 1; i >= 0; i--) {
      if (this.data[i].level === node.level - 1) {
        return this.data[i];
      }
    }
  }
}
