import {AfterContentInit, Component, forwardRef} from '@angular/core';
import {OptionsActivityManagerComponent} from 'primeng-iow/cdk/options-activity-manager';


@Component({
  selector: 'app-activity-manager-upgraded',
  template: '<ng-content></ng-content>',
  providers: [{
    provide: OptionsActivityManagerComponent,
    useExisting: forwardRef(() => ActivityManagerUpgradedComponent),
  }]
})
export class ActivityManagerUpgradedComponent<T> extends OptionsActivityManagerComponent<T> implements AfterContentInit {
  ngAfterContentInit(): void {
    super.ngAfterContentInit();
    this.keyManager.setFirstItemActive();
  }
}
