import {AfterContentInit, Directive, Input} from '@angular/core';
import {IOptionsScrollManagerComponentConfig, OptionsScrollManagerComponent} from 'primeng-iow/cdk/scrolling';
import {CdkVirtualScrollViewport} from '@angular/cdk/scrolling';
import {BaseOption} from 'primeng-iow/cdk/options';

@Directive({
  selector: '[appOptionsVirtualScrollManager]'
})
export class OptionsVirtualScrollManagerDirective extends OptionsScrollManagerComponent implements AfterContentInit {
  @Input('appOptionsVirtualScrollManager') config: IOptionsScrollManagerComponentConfig;

  constructor(public scrollable: CdkVirtualScrollViewport) {
    super();
  }

  scrollToOption(item: BaseOption<unknown>): void {
    const {start} = this.scrollable.getRenderedRange();
    const index = this.options.toArray().indexOf(item) + start;
    this.scrollToIndex(index);
  }
}
