import {ChangeDetectorRef, Component, ElementRef, Input, Optional, ViewChild} from '@angular/core';
import {BaseOption} from 'primeng-iow/cdk/options';
import {FocusableOption} from '@angular/cdk/a11y';
import {OptionsSelectionManagerComponent} from 'primeng-iow/cdk/options-selection-manager';

@Component({
  selector: 'app-focusable-option',
  templateUrl: './focusable-option.component.html',
  styleUrls: ['./focusable-option.component.css'],
  providers: [BaseOption.provideBaseOption(FocusableOptionComponent)]
})
export class FocusableOptionComponent extends BaseOption<string> implements FocusableOption {
  @Input() value;
  @ViewChild('label', {static: true}) label: ElementRef;

  constructor(protected cd: ChangeDetectorRef,
              @Optional() protected selectionManager: OptionsSelectionManagerComponent<string>) {
    super();
  }

  getLabel: () => string = () => this.value;

  focus(origin?: 'touch' | 'mouse' | 'keyboard' | 'program' | null): void {
    this.label.nativeElement.focus();
    this.activateViaInteraction();
  }
}
