import {ChangeDetectionStrategy, Component, Input} from '@angular/core';
import {BaseOption, HighlightableContentOption} from 'primeng-iow/cdk/options';

@Component({
  selector: 'app-highlightable-content-option',
  template: '{{getLabel()}}',
  providers: [BaseOption.provideBaseOption(HighlightableContentOptionUpgradedComponent)],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class HighlightableContentOptionUpgradedComponent<T> extends HighlightableContentOption<T> {
  // tslint:disable-next-line:no-input-rename
  @Input('getLabel') labelGetter: (value: T) => string;
  getLabel = () => this.labelGetter(this.value);
}
