import {Component, ViewChild} from '@angular/core';
import {CdkDropList, transferArrayItem} from '@angular/cdk/drag-drop';
import {FocusKeyManager, ListKeyManager} from '@angular/cdk/a11y';
import {Subject} from 'rxjs';
import {OptionsActivityManagerComponent} from 'primeng-iow/cdk/options-activity-manager';
import {DragDropComponent} from './drag-drop.component';
import {BaseOption} from 'primeng-iow/cdk/options';
import {IActivityManagerUpgradedComponentConfig} from '../shared/activity-manager-upgraded';

@Component({
  templateUrl: './drag-drop-navigation.component.html',
  styleUrls: ['./drag-drop.component.css'],
})
export class DragDropNavigationComponent extends DragDropComponent {
  FocusKeyManager = FocusKeyManager;
  keydownToDo$ = new Subject<KeyboardEvent>();
  keydownDone$ = new Subject<KeyboardEvent>();

  activityManagerConfig: IActivityManagerUpgradedComponentConfig = {
    withWrap: true,
    withTypeAhead: {debounceInterval: 0},
    selfService: true
  };

  @ViewChild('todoList', {static: true}) todoList: CdkDropList<string[]>;
  @ViewChild('doneList', {static: true}) doneList: CdkDropList<string[]>;
  @ViewChild('todoActivityManager', {static: true}) todoActivityManager: OptionsActivityManagerComponent<string>;
  @ViewChild('doneActivityManager', {static: true}) doneActivityManager: OptionsActivityManagerComponent<string>;

  markDone(index: number): void {
    this.updateActiveItem(index, this.todoActivityManager.keyManager);
    transferArrayItem<string>(this.todoList.data, this.doneList.data, index, 0);
  }

  markToDo(index: number): void {
    this.updateActiveItem(index, this.doneActivityManager.keyManager);
    transferArrayItem<string>(this.doneList.data, this.todoList.data, index, 0);
  }

  private updateActiveItem(index: number, keyManager: ListKeyManager<BaseOption<string>>) {
    if (index > 0) {
      keyManager.setPreviousItemActive();
    } else {
      keyManager.setActiveItem(1);
    }
  }
}
